<template>
  <div class="video-box" @click="playVideo">
    <video ref="videoEl" src="https://www.runoob.com/try/demo_source/movie.mp4"/>
    <img src="../../assets/images/icon-play.png"
         v-show="!isPlay"
         class="play-icon"/>
  </div>
</template>

<script>
// import 'cai-tools'
export default {
  name: 'video-control',
  data () {
    return {
      isPlay: false, // 是否在播放
      videoEl: undefined // 视频播放元素
    }
  },
  mounted () {
    if (this.videoEl === undefined) {
      this.videoEl = this.$refs.videoEl
      // 播放开始触发
      this.videoEl.addEventListener('play', () => {
        this.isPlay = true
      })
      // 播放结束，或者暂停触发
      this.videoEl.addEventListener('pause', () => {
        this.isPlay = false
      })
    }
  },
  methods: {
    /**
       * @description: 播放视频
       * @date 2020/2/12
       */
    playVideo () {
      if (this.videoEl.paused) {
        this.videoEl.play()
      } else {
        this.videoEl.pause()
      }
    }
  },
  deactivated () {
    this.videoEl = undefined
  }
}
</script>

<style scoped lang="less">
  .video-box {
    width: 100%;
    overflow: hidden;
    border-radius:0.357rem;
    position: relative;

    /*播放按钮图标*/

    .play-icon {
      width: 2rem;
      height: 2.786rem;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
    }

    video {
      width: 100%;
      height: 100%;
      object-fit:fill;
      border-radius:0.357rem;
    }
  }
</style>
